.product-card {
  width: 100%;
  min-width: 260px;
  height: 386px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding: 22px 30px;
  background-position: 100%;
  background-repeat: no-repeat;
  cursor: pointer;
  overflow-y: auto;



  &::-webkit-scrollbar {
    display: none;
  }

  @media (max-width: 1023px) {
    padding: 26px 35px;
    max-width: 309px;
    max-height: 386px;
    width: 100%;
  }

  @media (max-width: 767px) {
    padding: 18px 25px;
    max-width: none;
    max-height: none ;
    width: 100%;
    height: 100%;
    aspect-ratio: 270 / 386;
    // height: calc(386px / (320px) * (100vw));
    // margin: 0 auto;
  }

  &:hover {
    justify-content: flex-start;
    background: #011c40;
    box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
    padding: 28px 25px 35px 25px;
    cursor: default;

    @media (max-width: 1023px) {
      padding: 28px 35px 35px 35px;
    }

    @media (max-width: 767px) {
      padding: 28px 25px 35px 25px;
    }

  }

  &:hover .product-card__route,
  &:hover .product-card__port,
  &:hover .product-card__duration,
  &:hover .product-card__price,
  &:hover .product-card__button {
    display: block;
  }
}

.product-card--one {
  background-image: image-set(url("../img/cardblock/card1/card1.jpg") 1x,
      url("../img/cardblock/card1/card1x2.jpg") 2x);
  background-repeat: no-repeat;
  background-size: cover;
}

.product-card__name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: #f9fbfd;
}

.product-card__route {
  margin: 0;
  margin-top: 14px;
  display: none;
}

.product-card__route h4 {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  display: flex;
  align-items: center;
  color: #f9fbfd;
}

.product-card__route p {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  display: flex;
  align-items: center;
  color: #d8d8d9;
  margin: 0;
}

.product-card__port {
  margin-top: 14px;
