  color: #d8d8d9;
}

.product-card__button {
  margin: 0 auto;
  margin-top: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  max-width: 210px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  align-items: center;
  color: #011c40;
  background: #f9fbfd;
  display: none;
  cursor: pointer;

  @media (max-width: 1023px) {
    max-width: 239px;
  }

  &:hover {
    background-color: #0054c2;
    color: #ffffff;
  }
}

